<template>
  <h2 class="txt_center mt-4">test view</h2>

  <div class="inner">
    <!--    <div v-for="item in Items" :key="item" class="add_area mt-4">-->
    <div class="add_area">
      <div class="head">
        <h3>{{ Items.title }}</h3>
      </div>
      <div class="content">
        <p>{{ Items.content }}</p>
      </div>
    </div>
  </div>

  <div class="btn_group txt_center mt-4">
    <router-link :to="`/List`">돌아가기</router-link>
  </div>
</template>

<script setup>
  import { ref } from 'vue';
  import axios from 'axios';
  import { useRoute } from 'vue-router';

  const Items = ref([]);
  const route = useRoute();

  const getItems = async () => {
    try {
      const res = await axios.get('http://localhost:3000/Items/' + route.params?.id);
      Items.value = res.data;
    } catch (err) {
      console.log(err);
    }
  };

  getItems();
</script>

<style>
@import "../assets/style.css";
</style>