<template>
  <h2 class="txt_center mt-4">test board</h2>
  <!-- [D] 게시판 영역 -->
  <div class="inner">
    <div>
      <div class="btn_group" style="justify-content: flex-end;">
        <router-link :to="`/Add`">등록</router-link>
      </div>
      <ul class="board">
        <li class="th col-group">
          <div class="num">번호</div>
          <div class="title">제목</div>
          <div class="btn">삭제</div>
        </li>
        <li v-for="(item, index) in Items" :key="item" class="td col-group">
          <div class="num">{{ item.id }}</div>
          <div class="title">
            <router-link :to="{ name: 'Detail', params: { id: item.id } }">{{ item.title }}</router-link>
          </div>
          <!-- <div class="date">{{ dayjs(item.created_at).format('YYYY.MM.DD') }}</div> -->
          <div class="btn"><button @click="deleteBtn(index)">삭제</button></div>
        </li>
      </ul>
      <div class="pagination col-group">
        <a v-if="currentPage !== 1" @click="getItems(currentPage - 1)">이전</a>
        <a
          v-for="page of numberOfPages"
          :key="page"
          :class="currentPage === page ? 'active' : ''"
          @click="getItems(page)"
        >
          {{ page }}
        </a>
        <a v-if="numberOfPages !== currentPage" @click="getItems(currentPage + 1)">다음</a>
      </div>
    </div>
  </div>
  <!-- //게시판 영역 -->
</template>

<script setup>
  import { computed, ref } from 'vue';
  import axios from 'axios';
  // import dayjs from 'dayjs';

  const Items = ref([]);
  const limit = 10;
  const numberOfItems = ref(0);
  const currentPage = ref(1);

  const numberOfPages = computed(() => { // eslint-disable-line no-unused-vars
    return Math.ceil(numberOfItems.value / limit);
  });

  const getItems = async (page = currentPage.value) => {
    currentPage.value = page;

    try {
      const res = await axios.get(`
      http://localhost:3000/Items?_page=${page}&_limit=${limit}`
      );
      numberOfItems.value = parseInt(res.headers['x-total-count']);
      Items.value = res.data;
    } catch (err) {
      console.log(err);
    }
  };

  getItems();

  const deleteBtn = async (index) => { // eslint-disable-line no-unused-vars
    const id = Items.value[index].id;
    try {
      if (window.confirm('정말 삭제 하시겠습니까?')) {
        await axios.delete('http://localhost:3000/Items/' + id);
        Items.value.splice(index, 1);
      }
    } catch (err) {
      console.log(err);
    }
  };


</script>

<style>
@import "../assets/style.css";
</style>
