<template>
  <h2 class="txt_center mt-4">test add</h2>

  <div class="inner">
    <div class="add_area mt-4">
      <form @submit.prevent="onSubmit">
        <div class="head">
          <input v-model="title" type="text" placeholder="제목을 입력해주세요" />
        </div>
        <div class="content">
          <textarea v-model="content" placeholder="내용을 입력해주세요"> </textarea>
        </div>
        <button class="mt-4" type="submit">등록 ㄱ고고</button>
      </form>
    </div>
  </div>

  <div class="btn_group txt_center mt-4">
    <router-link :to="`/List`">돌아가기</router-link>
  </div>
</template>

<script setup>
  import { ref } from 'vue';
  import axios from 'axios';

  const title = ref('');
  const content = ref('');

  const onSubmit = () => { // eslint-disable-line no-unused-vars
    if (title.value == '') {
      alert('제목을 입력해주세요');
    } else if (content.value == '') {
      alert('내용을 입력해주세요');
    } else {
      axios
        .post('http://localhost:3000/Items', {
          title: title.value,
          content: content.value,
        })
        .then((res) => {
          console.log(res);
          location.reload();
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };
</script>

<style>
@import "../assets/style.css";
</style>